<template>
    <div class="container">
            <!-- <div class="circles-top">
                '
                <div class="circle" v-for="i in 10" :key="i">
                    
                </div>
        </div> -->
     <div class="main-box">
            <h1 class="">{{questions[currentQuestion].description}}</h1>
            <div class="options">
            <div class="option-box" v-for="(op,index) in questions[currentQuestion].ops"  :key="index">
                <button class="option-text" @click="getInput(index)">{{op[0]}}</button>
                <!-- <div ><img class="option-img" :src="op[1]" alt=""></div> -->
            </div>
            </div>
            <button class="back-button" @click="goBack()">Back</button>
            <button class="submit-button" @click="submit()">Submit</button>
           
        </div> 
    </div>
</template>

<script>
import questions from '../data/quiz-questions';

export default {
    components: {
    },
    data() {
        return{
            questions,
            // questionsDone:0,
            selectedOptions:[],
            currentQuestion:0,
        }
    },
    methods: {
        getInput(index){
            this.selectedOptions.push(index)
            console.log("options = " + this.selectedOptions)
            this.currentQuestion++
        },
        goBack(){
            if(this.currentQuestion<=0) return;
            this.currentQuestion--;
            this.selectedOptions.pop()
        },
        submit (){
            this.$http.post('https://quiz-game-8a743.firebaseio.com/datsa.json', this.selectedOptions)
            .then(response => { console.log(response)}, error => { console.log(error)});
        },
    }
}
</script>

<style scoped>
.intro {
    padding: 10px;
}
.back-button {
    background: none;
    outline: none;
    border: 2px solid coral;
    border-radius: 20px;
    width: 150px;
    height: 50px;
   
   position: absolute;
   top: 30px;
   left: 30px;

}
.back-button:hover{
    transition: 0.2s;
    background: red;
    color: white;
}
.circle {
    background: orange;
    padding: 2px;
    border-radius: 15px;
    width: 15px;
    height: 15px;

    float: left;
}
.circles-top{
    width: 20px;
    height: 20px;
    float: left;
    margin: auto;
}
.main-box {
    display: table;
    padding-top: 20px;
    padding-bottom: 50px;
    margin-left: auto;
    margin-right: auto;
    border-radius: 5px;
    box-shadow:  0px 2px 5px #9e9e9e, 
             2px 0px 5px #9e9e9e;   
    height: 700px;
    width: 400px;
}
.container {
    padding: 50px;
    padding-top: 0px;
    margin-top: 0px;
}
.options {
    padding: 0px 20px;

}
.option-img {
    width: 100px;
    border-radius: 10px;
}
.option-box {
    color: lightcoral;
    margin: 10px;
    float: left;
    width: 35%;
    height: 100px;
    border: 2px solid coral;
    border-radius: 10px;
    font-size: 15px;
    cursor: pointer;
}
.option-box:hover {
    transition: 0.2s;
    background: #9e9e9e;
}
.option-text {
    width: 100%;
    height: 100%;
    border-radius: 10px;
    background: none;
    border: none;
    outline: none;
    text-size-adjust: 100px;
    font-size: 15px;
    font-weight: 100px;
}
@media screen and (max-width: 576px) {
    .main-box {
        margin-left:auto ;
        margin-right:auto ;
        width: 100%;
    } 
    .container {
        padding-top: 50px;
        padding-right: 10px ;
        padding-left: 10px ;
        margin-left: auto;
        margin-right: auto;
    }
    .option-box:hover{
        background: none;
    }
}
</style>