import Vue from 'vue'
import Router from 'vue-router'

import HomePage from '../components/HomePage.vue';
import MakeQuiz from '../components/make-quiz.vue';


Vue.use(Router);

export default new Router({
    routes: [
        {
            path: "/",
            name: 'HomePage',
            component: HomePage
        },
        {
            path: "/make-quiz",
            name: 'MakeQuiz',
            component: MakeQuiz,
        },
    ],
})